import rewriteURL from "./url";
export function request(headers, requestURL) {
    headers["host"] = requestURL.host;
    return headers;
}
export function response(headers, requestURL) {
    ["cache-control", "Content-Security-Policy", "Content-Security-Policy-Report-Only", "content-encoding", "content-length", "cross-origin-opener-policy", "cross-origin-opener-policy-report-only", "report-to", "strict-transport-security", "x-content-type-options", "x-frame-options"].forEach((header) => {
        delete headers[header];
        delete headers[header.toLowerCase()];
    });
    headers["Location"] = rewriteURL(headers["Location"] || headers["location"]);
    return headers;
}
