import { parse, walk, generate } from "css-tree";
import rewriteURL from "./url";
export default function rewriteCSS(css, origin) {
    const ast = parse(css);
    walk(ast, (node) => {
        if (node.type === "Url") {
            node.value = rewriteURL(node.value, origin);
        }
    });
    return generate(ast);
}
